// components/Sidebar.tsx
'use client';

import { useRouter } from 'next/navigation';
import Image from 'next/image';
import { LogOut, LayoutDashboard, User } from 'lucide-react';

interface SidebarProps {
  currentPage: 'todos' | 'profile';
}

export default function Sidebar({ currentPage }: SidebarProps) {
  const router = useRouter();

  const handleLogout = () => {
    localStorage.removeItem('currentUser');
    router.push('/login');
  };

  const userData = typeof window !== 'undefined' ? localStorage.getItem('currentUser') : null;
  const user = userData ? JSON.parse(userData) : null;

  return (
    <div className="w-64 bg-white shadow-lg min-h-screen flex flex-col">
      {/* User Profile - Moved to top */}
      <div className="p-6 border-b">
        <div className="flex items-center space-x-4 mb-6">
          <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
            {user?.profilePhoto ? (
              <Image
                src={user.profilePhoto}
                alt="Profile"
                width={48}
                height={48}
                className="rounded-full object-cover"
              />
            ) : (
              <User size={24} className="text-blue-600" />
            )}
          </div>
          <div className="flex-1 min-w-0">
            <p className="text-sm font-medium text-gray-900 truncate">
              {user?.firstName} {user?.lastName}
            </p>
            <p className="text-sm text-gray-500 truncate">{user?.email}</p>
          </div>
        </div>

        {/* Logout Button - Moved higher */}
        <button
          onClick={handleLogout}
          className="w-full flex items-center space-x-3 px-4 py-3 rounded-lg text-left text-gray-700 hover:bg-red-50 hover:text-red-700 transition-colors border border-gray-200"
        >
          <LogOut size={20} />
          <span className="font-medium">Logout</span>
        </button>
      </div>

      {/* Navigation */}
      <div className="flex-1 p-4">
        <nav className="space-y-2">
          <button
            onClick={() => router.push('/todos')}
            className={`w-full flex items-center space-x-3 px-4 py-3 rounded-lg text-left transition-colors ${
              currentPage === 'todos'
                ? 'bg-blue-100 text-blue-700 border-r-2 border-blue-600'
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <LayoutDashboard size={20} />
            <span className="font-medium">Dashboard</span>
          </button>

          <button
            onClick={() => router.push('/profile')}
            className={`w-full flex items-center space-x-3 px-4 py-3 rounded-lg text-left transition-colors ${
              currentPage === 'profile'
                ? 'bg-blue-100 text-blue-700 border-r-2 border-blue-600'
                : 'text-gray-700 hover:bg-gray-100'
            }`}
          >
            <User size={20} />
            <span className="font-medium">Profile</span>
          </button>
        </nav>
      </div>
    </div>
  );
}